<template>
    <div class="shopbag">
        <van-nav-bar title="购物袋" left-arrow fixed @click-left="back" @click-right="isEdit = !isEdit">
            <template #right>
                <div>{{ isEdit ? "完成" : "编辑" }}</div>
            </template>
        </van-nav-bar>

        <div class="shopbag-box">
            <div class="shopbag-bg"></div>

            <div class="shopbag-product">
                <div class="shopbag-product-item" v-for="(item,index) in shopbagData" :key="index">
                    <van-swipe-cell :disabled="!isEdit">
                        <template #default>
                            <div class="cell-item-box">
                                <div class="check-box">
                                    <van-checkbox v-model="item.isChecked" checked-color="#0C34BA" @change="simpleSelect"/>
                                </div>
                                <div class="pro-info-box">
                                    <div class="pro-img">
                                        <img class="auto-img" :src="item.small_img" alt="">
                                    </div>
                                    <div class="pro-info">
                                        <div class="pro-info-text">
                                            <div class="info-text-names">
                                                <div class="text-names-name">{{ item.name }}</div>
                                                <div class="text-names-rule">{{ item.rule }}</div>
                                            </div>
                                            <div class="info-text-enname">{{ item.enname }}</div>
                                        </div>

                                        <div class="pro-count-box">
                                            <div class="pro-price">￥{{ item.price }}</div>
                                            <div class="step-box">
                                                <van-stepper v-model="item.count" theme="round" disable-input button-size="20" @change="changeCount(item)" />
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </template>

                        <template #right>
                            <van-button square text="删除" type="danger" class="delete-button" @click="removeOne(index, item.sid)" />
                        </template>
                    </van-swipe-cell>
                </div>
            </div>
            <van-submit-bar v-if="!isEdit" :price="total" button-text="提交订单" button-color="#0C34BA" @submit="commit">
                <van-checkbox v-model="isAllChecked" checked-color="#0C34BA" @click="allSelect">全选</van-checkbox>
            </van-submit-bar>

            <van-submit-bar v-else button-text="删除选择" button-color="#EE0A24" @submit="removeSelected">
                <van-checkbox v-model="isAllChecked" @click="allSelect">全选</van-checkbox>
            </van-submit-bar>
        </div>
    </div>
</template>

<script>
    export { default } from './shopbag';
</script>

<style lang="less" scoped>
    @import './shopbag.less';
</style>