import {reactive,toRefs,onMounted,defineComponent,computed} from 'vue';
import {useRouter} from 'vue-router';
import {getRequest,postRequest} from '../../../request/api';
import url from '../../../request/url';
import {Toast} from 'vant'
export default defineComponent({
    name:'shopbag',
    setup () {

        let state = reactive({
            isEdit: false,//是否处于编辑状态
            isAllChecked: false,
            allShopbagData: [], //所有购物袋数据
            shopbagData:[],
            isSelectProduct:computed(() => state.shopbagData.some(item => item.isChecked)),//判断是否勾选商品

        })
        //订单总金额
        let total = computed(() => {
            let total = 0;
            state.shopbagData.forEach(item => {
                if(item.isChecked) total += item.price * item.count;
            })
            return total * 100;
        })

        let {getShopbagData,back,simpleSelect,allSelect,changeCount,removeOne,commit,removeSelected} = methodes(state);

        onMounted(() => {
            getShopbagData();
        })

        return {
            ...toRefs(state),
            total,//计算属性
            back,
            simpleSelect,
            allSelect,
            changeCount,
            removeOne,
            commit,
            removeSelected,
            
        }
    }
})


function methodes (state) {
    let router = useRouter();
    let getShopbagData = () =>{
        getRequest(url.findAllShopcart).then(({code,result = []}) => {
            if(code == 5000){
                result.map(item => item.isChecked = false);
                state.allShopbagData = result;

                state.shopbagData = result;
            }else{

            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    let back = () => {
        
        router.back();
       
    }
    //单选
    let simpleSelect = () => {
        Reflect.set(state,'isAllChecked',state.shopbagData.every(item => item.isChecked));
    }
    //全选
    let allSelect = () => {
        state.shopbagData.forEach(item => Reflect.set(item,'isChecked',Reflect.get(state,'isAllChecked')));
    }
    //修改数量
    let changeCount = ({sid,count}) => {
        postRequest(url.modifyShopcartCount,{sid,count}).then(({code,msg}) => {
            if(code == 6000){

            }else{
                Toast(msg)
            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }
    //单个删除
    let removeOne = (index, sid) => {
        postRequest(url.deleteShopcart,{sids:JSON.stringify([sid])}).then(({code,msg}) => {
            if(code == 7000) Reflect.apply(state.shopbagData.splice,state.shopbagData,[index,1]);
            Toast(msg)
        }).catch((err) => {
            console.log("err==>",err);
        });
    }
    //提交订单，跳转到订单页面
    let commit = () =>{
        if(!state.isSelectProduct) return Toast('请选择购买的商品');
        //sids: 勾选商品的购物袋sid集合
        let sids = [];
        state.shopbagData.map(item => item.isChecked && sids.push(item.sid));
        //跳转到结算页面且携带sids查询参数
        router.push({ name: "pay", query: { sids: sids.join("-") } });
    }
    //删除选择
    let removeSelected = () => {
        if(!state.shopbagData.some(item => item.isChecked)) return Toast('请选择购买的商品');
        //sids: 勾选商品的购物袋sid集合
        let sids = [];
        state.shopbagData.map(item => item.isChecked && sids.push(item.sid));
        let data = Object.create(null);
        Reflect.set(data,'sids',JSON.stringify(sids));
        postRequest(url.deleteShopcart,data).then(({code,msg}) => {
            code == 7000 && state.shopbagData.map((item,index) => item.isChecked && state.shopbagData.splice(index,1));
            Toast(msg);
        }).catch((err) => {
            console.log("err==>",err);
        });
    }


    return{
        getShopbagData,
        back,
        simpleSelect,
        allSelect,
        changeCount,
        removeOne,
        commit,
        removeSelected,
        
        
    }
}